import Card from 'react-bootstrap/Card';
import Autoportrait from '../images/miniature-autoportrait.png';

function Projet () {
  return (
    <Card className="bg-dark text-black">
    <Card.Img src={Autoportrait} alt="Test imgSimon" />
    <Card.ImgOverlay>
      <Card.Title>Titre projet 1</Card.Title>
      <Card.Text>
        paragraphe 1
      </Card.Text>
      <Card.Text>paragraphe 2</Card.Text>
    </Card.ImgOverlay>
  </Card>
  );
}

export default Projet;
